.travel-form {
    width: 70.5%;
    background: $white;
    box-shadow: 0px 10px 30px 5px rgba(219, 200, 197, 0.2);
    display: flex;
    align-items: center;
    position: absolute;
    bottom: -77px;
    right: 0px;
    padding: 45px;
    &__filter{
        border: none;
        width: 81.6%;
    }
    &__discover{
        cursor: pointer;
        font-weight: 500;
        font-size: 18px;
        line-height: 25px;
        color: $white;
        background: $coral;
        padding: 20px 37px;
    }
}